@mixin transition($property: all, $duration: 0.45s, $ease: cubic-bezier(0.65, 0, 0.076, 1)) {
    transition: $property $duration $ease;
}

.btn-more {
    position: absolute;
    display: inline-block;
    cursor: pointer;
    outline: none;
    border: 0;
    vertical-align: middle;
    text-decoration: none;
    background: transparent;
    padding: 0;
    font-size: inherit;
    font-family: inherit;
    &.learn-more {
        width: 12rem;
        height: auto;
        .circle {
            @include transition(all, 0.45s, cubic-bezier(0.65, 0, 0.076, 1));
            position: absolute;
            display: block;
            margin: 0;
            width: 3rem;
            height: 3rem;
            background: var(--white);
            border-radius: 1.625rem;
            .icon {
                @include transition(all, 0.45s, cubic-bezier(0.65, 0, 0.076, 1));
                position: absolute;
                top: 0;
                bottom: 0;
                margin: auto;
                background: var(--black);
                &.arrow {
                    @include transition(all, 0.45s, cubic-bezier(0.65, 0, 0.076, 1));
                    left: 0.625rem;
                    width: 1.125rem;
                    height: 0.125rem;
                    background: none;
                    &::before {
                        position: absolute;
                        content: '';
                        top: -0.25rem;
                        right: 0.0625rem;
                        width: 0.625rem;
                        height: 0.625rem;
                        border-top: 0.125rem solid var(--black);
                        border-right: 0.125rem solid var(--black);
                        transform: rotate(45deg);
                    }
                }
            }
        }
        .button-text {
            @include transition(all, 0.45s, cubic-bezier(0.65, 0, 0.076, 1));
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            padding: 0.75rem 0;
            margin: 0 0 0 1.85rem;
            color: var(--white);
            font-weight: 700;
            line-height: 1.6;
            text-align: center;
            text-transform: uppercase;
        }
    }
    &:hover {
        .circle {
            width: 100%;
            .icon {
                &.arrow {
                    background: var(--black);
                    transform: translate(1rem, 0);
                }
            }
        }
        .button-text {
            color: var(--black);
        }
    }
}
